export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		}
	},
	methods: {

	},
	onShow() {
		// 使用挂在方法获取用户数据
		let globalUser = this.getGlobalUser('globalUser')
		if (globalUser !== null) {
			this.userIsLogin = true
			this.userInfo = globalUser
		} else {
			this.userIsLogin = false
			this.userInfo = {}
		}
	}
}
